<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :modal="false"
      top="17vh"
    >
      <el-form :model="form">
        <el-form-item
          label="企业"
          :label-width="formLabelWidth"
          v-if="scpoeType == 0"
        >
          <el-select
            v-model="form.companyId"
            placeholder=""
            @change="equipmentListFun"
          >
            <el-option
              v-for="item in companyOptions"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="企业" :label-width="formLabelWidth" v-else>
          <el-input
            v-model="form.companyId"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="设备" :label-width="formLabelWidth">
          <el-select v-model="form.eqmId" placeholder="">
            <el-option
              v-for="item in eqmOptions"
              :key="item.eqmId"
              :label="item.eqmName"
              :value="item.eqmId"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="能源类型" :label-width="formLabelWidth">
          <el-select v-model="form.energyType2" placeholder="">
            <el-option
              v-for="item in energyOption"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="能源单位" :label-width="formLabelWidth">
          <el-input v-model="form.unitId" autocomplete="off" @mousewheel.native.prevent @DOMMousewheel.native.prevent></el-input>
        </el-form-item>
        <el-form-item label="工作日基准" :label-width="formLabelWidth">
          <el-input
            v-model="form.workDayStandardQty"
            @mousewheel.native.prevent @DOMMousewheel.native.prevent
            type="number"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="休息日基准" :label-width="formLabelWidth">
          <el-input
            type="number"
            @mousewheel.native.prevent @DOMMousewheel.native.prevent
            v-model="form.restDayStandardQty"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label="计划产值" :label-width="formLabelWidth">
          <el-input
            v-model="form.planOutPutQty"
            autocomplete="off"
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="起始日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.startDate"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="截止日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.endDate"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            :clearable="false"
          >
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirm()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  energyCompanyList,
  equipmentList,
  energyAlarmAddUpdateDelete,
} from "@/network/warnMonitor";
export default {
  data() {
    return {
      dialogFormVisible: false,
      form: {
        companyId: "",
        eqmId: "",
        eqmName: "",
        energyType2: "",
        unitId: "",
        workDayStandardQty: "",
        restDayStandardQty: "",
        planOutPutQty: "",
        startDate: "",
        endDate: "",
      },
      rowObj: {},
      formLabelWidth: "100px",
      companyOptions: [],
      eqmOptions: [],
      energyOption: [
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      scpoeType: 0, //当前操作的状态（0:新增，1:修改）
    };
  },
  watch: {
    dialogFormVisible(val) {
      if (val == false) {
        this.form = {
          companyId: "",
          eqmId: "",
          eqmName: "",
          energyType2: "",
          unitId: "",
          workDayStandardQty: "",
          restDayStandardQty: "",
          planOutPutQty: "",
          startDate: "",
          endDate: "",
        };
      }
    },
  },
  mounted() {},
  methods: {
    showDialogFun(type, row) {
      this.dialogFormVisible = true;
      if (sessionStorage.getItem("companyType") == "企业") {
        this.companyOptions = [
          {
            companyId: sessionStorage.getItem("companyId"),
            companyName: sessionStorage.getItem("companyName"),
          },
        ];
      } else {
        this.energyCompanyListFun();
      }

      this.scpoeType = type;
      //修改的时候 数据需要回显
      if (type == 1) {
        this.rowObj = row;
        this.form = {
          companyId: row.companyId,
          eqmId: row.eqmId,
          eqmName: row.eqmName,
          energyType2: row.energyType2,
          unitId: row.unitId,
          workDayStandardQty: row.workDayStandardQty,
          restDayStandardQty: row.restDayStandardQty,
          planOutPutQty: row.planOutPutQty,
          startDate: row.startDate,
          endDate: row.endDate,
        };
        this.equipmentListFun();

        this.form.eqmId = row.eqmId;
      }
    },
    /**
     * 获取能源企业列表
     * **/
    energyCompanyListFun() {
      energyCompanyList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.companyOptions = content;
        })
        .catch((err) => {});
    },
    /**
     * 获取能源企业下的设备列表
     * **/
    equipmentListFun() {
      this.form.eqmId = ""; //重置设备Id
      this.form.eqmName = ""; //重置设备名称
      equipmentList(this.form.companyId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.eqmOptions = content;
        })
        .catch((err) => {});
    },
    confirm() {
      //判断当前状态
      if (this.scpoeType == 0) {
        this.addAlarmFun();
      } else {
        this.editAlarmFun();
      }
    },
    addAlarmFun() {
      let filterObj = this.eqmOptions.filter(
        (item) => item.eqmId == this.form.eqmId
      );
      this.form.eqmName = filterObj[0].eqmName;
      let dateStr = "";
      let keys = Object.keys(this.form);
      keys.forEach((item) => {
        dateStr += item + ":" + this.form[item] + ",";
      });
      energyAlarmAddUpdateDelete("", dateStr, this.scpoeType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "新增成功!",
              type: "success",
            });
            this.$bus.$emit("energyWarnrefresh", true);
            this.dialogFormVisible = false;
          }
        })
        .catch((err) => {});
    },
    editAlarmFun() {
      let filterObj = this.eqmOptions.filter(
        (item) => item.eqmId == this.form.eqmId
      );
      this.form.eqmName = filterObj[0].eqmName;
      let dateStr = "";
      let keys = Object.keys(this.form);
      keys.forEach((item) => {
        dateStr += item + ":" + this.form[item] + ",";
      });
      energyAlarmAddUpdateDelete(this.rowObj.alarmId, dateStr, this.scpoeType)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "修改成功!",
              type: "success",
            });
            this.$bus.$emit("energyWarnrefresh", true);
            this.dialogFormVisible = false;
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
/deep/ .el-dialog {
  background: #000000de;
  box-shadow: 2px 2px 10px #58585826;
  width: 40%;
}
/deep/ .el-dialog__headerbtn .el-dialog__close {
  color: #fff;
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.dialog_content {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
/deep/ .el-form-item__label {
  color: #ffffff;
}
/deep/ .el-select {
  width: 100% !important;
}
/deep/ .el-input__inner,
/deep/ .el-date-editor.el-input {
  width: 100% !important;
}

/deep/ .el-message__icon {
  font-size: 16px !important;
}
.el-form {
  display: flex;
  flex-wrap: wrap;
}
.el-form-item {
  width: 50%;
  margin-bottom: 22px;
}

/deep/ .el-input.is-disabled .el-input__inner {
  background-color: transparent;
  border-color: rgba(34, 175, 237, 0.5);
  color: #c0c4cc;
  cursor: not-allowed;
}

 // 处理input type = number的上下箭头
/deep/ input::-webkit-outer-spin-button,
/deep/ input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
/deep/ input[type="number"]{
  -moz-appearance: textfield;
}

</style>