<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="energyAlarmQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="energyAlarmQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <el-button
          type="primary"
          icon="el-icon-plus"
          style="
            background-color: #0066ff;
            border: none;
            width: 1.5rem;
            position: absolute;
            right: 0.2rem;
          "
          @click="addInfo"
          >新增</el-button
        >
      </div>
      <div class="tabel">
        <el-table
          :data="tableData"
          style="width: 100%"
          height="250"
          @row-click="energyAlarmLineFun"
        >
          <el-table-column prop="alarmId" label="预警数据编号" width="150">
          </el-table-column>
          <el-table-column prop="companyId" label="企业编号" width="170">
          </el-table-column>
          <el-table-column prop="eqmId" label="设备编号" width="150">
          </el-table-column>
          <el-table-column prop="eqmName" label="设备名称"> </el-table-column>
          <el-table-column prop="department" label="部门"> </el-table-column>
          <el-table-column prop="energyType2" label="能源类型">
          </el-table-column>
          <el-table-column prop="unitId" label="单位"> </el-table-column>
          <el-table-column
            prop="workDayStandardQty"
            label="工作日基准值"
            width="170"
          >
          </el-table-column>
          <el-table-column
            prop="restDayStandardQty"
            label="休息日基准值"
            width="170"
          >
          </el-table-column>
          <el-table-column prop="planOutPutQty" label="计划产值">
          </el-table-column>
          <el-table-column prop="startDate" label="起始日期" width="150">
          </el-table-column>
          <el-table-column prop="endDate" label="截止日期" width="150">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="editInfo(scope.row)"
                >修改</el-button
              >
              <el-button type="text" size="small" @click="deleteInfo(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          style="margin-top: 0.1rem; margin-bottom: 0.1rem"
          background
          layout="prev, pager, next"
          :total="total"
          @current-change="handleCurrentChange"
        >
        </el-pagination>
      </div>
      <div class="lineTable">
        <div>
          <el-table :data="alarmQty" style="width: 100%" height="100%">
            <el-table-column prop="alarmId" label="预警数据编号" width="150">
            </el-table-column>
            <el-table-column prop="date" label="日期" width="170">
            </el-table-column>
            <el-table-column prop="time" label="时间"> </el-table-column>
            <el-table-column prop="alarmLevel" label="预警等级">
            </el-table-column>
            <el-table-column prop="actualQty" label="实际值"> </el-table-column>
            <el-table-column prop="standardQty" label="基准值">
            </el-table-column>
          </el-table>
        </div>
        <div>
          <el-table :data="planOutPuts" style="width: 100%" height="100%">
            <el-table-column prop="date" label="日期" width="150">
            </el-table-column>
            <el-table-column prop="time" label="时间" width="170">
            </el-table-column>
            <el-table-column prop="planOutPutQty" label="计划产值">
            </el-table-column>
            <el-table-column
              prop="planEnergyUse"
              label="计划产值能耗"
              width="170"
            >
            </el-table-column>
            <el-table-column
              prop="planEnergyUseOutPutQty"
              label="单位产能能耗"
              width="170"
            >
            </el-table-column>
            <el-table-column prop="outPutQty" label="实际产值" width="170">
            </el-table-column>
            <el-table-column prop="energyUse" label="实际产值能耗" width="170">
            </el-table-column>
            <el-table-column
              prop="energyUseOutPutQty"
              label="单位产能能耗"
              width="170"
            ></el-table-column>
            <el-table-column
              prop="alarmLevel"
              label="预警等级"
            ></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <energyWarnAddDialog ref="addDialog"></energyWarnAddDialog>
  </div>
</template>
<script>
import { places } from "@/network/energyTrendAnalysis";
import { energyAlarmQuery, energyAlarmLine,energyAlarmAddUpdateDelete } from "@/network/warnMonitor";
import { Row } from "element-ui";
import energyWarnAddDialog from "./energyWarn_dialog.vue";
export default {
  components: {
    energyWarnAddDialog,
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      date1: "",
      date2: "",
      tableData: [],
      arry: {},
      total: 0,
      currentPage: 0,
      alarmQty: [],
      planOutPuts: [],
    };
  },
  created() {
    this.$bus.$on("energyWarnrefresh", (val) => {
      this.energyAlarmQueryFun();
    });
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    /**
     * 获取当前用户的地区企业层级信息
     * **/
    placesFun() {
      places()
        .then((res) => {
          let flag = false;
          res.content.forEach((item) => {
            let idFirst = item.id.substring(0, 1);
            if (idFirst == "A" || idFirst == "C") {
              item["isClick"] = true;
              if (!flag) {
                this.areaId = item.id;
                flag = true;
              }
            } else {
              item["isClick"] = false;
            }
          });
          this.data = this.$arrToTree(res.content);
          this.energyAlarmQueryFun();
        })
        .catch((err) => {});
    },
    /**
     * 点击tree节点
     * **/
    nodeClick(item) {
      if (item.isClick) {
        this.areaId = item.id;
        this.energyAlarmQueryFun();
      }
    },
    /**
     * 能耗告警信息列表
     * **/
    energyAlarmQueryFun() {
      energyAlarmQuery(this.date1, this.areaId, this.currentPage, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.alarmQty = this.planOutPuts = [];
            this.energyAlarmLineFun(content[0]);
          }
        })
        .catch((err) => {});
    },
    /**
     * 根据能源告警信息获取基准值预警与计划产值能耗预警
     * **/
    energyAlarmLineFun(row) {
      energyAlarmLine(row.alarmId)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.alarmQty = content.alarmQty;
            this.planOutPuts = content.planOutPuts;
          }
        })
        .catch((err) => {});
    },
    /**
     * 分页切换
     * **/
    handleCurrentChange(val) {
      this.currentPage = val;
      this.energyAlarmQueryFun();
    },
    /**
     *新增能源告警信息
     * **/
    addInfo() {
      this.$refs.addDialog.showDialogFun(0);
    },
    /**
     * 修改能源告警信息
     * **/
    editInfo(row) {
      this.$refs.addDialog.showDialogFun(1,row);
    },
    /**
     * 删除模板
     * **/
    deleteInfo(row) {
      this.$alert("确认删除该条信息", "", {
        confirmButtonText: "确定",
        callback: (action) => {
          this.deleteAlarmFun(row);
        },
      });
    },
    deleteAlarmFun(row) {
      energyAlarmAddUpdateDelete(row.alarmId,'',2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.$message({
              message: "删除成功!",
              type: "success",
            });
            this.energyAlarmQueryFun()
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
  flex-shrink: 0;
}
.right {
  width: calc(75% - 10px - 0.4rem);
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
  position: relative;
}
.lineTable {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  > div {
    width: 49.5%;
  }
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  margin: 0.2rem 0 0;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgb(0, 60, 158);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.2rem;
  border: none;
  cursor: pointer;
  height: 100%;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
/deep/ .el-input__inner {
  color: #fff;
}
/deep/ .el-input__icon {
  color: #00b4fe;
  font-size: 18px;
}

.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
  cursor: pointer;
}
.tabAll {
  display: flex;
  div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
  div {
    font-size: 0.17rem;
    padding: 0.1rem 0.3rem;
    border-radius: 0.04rem 0.04rem 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    margin: 0 0.02rem;
    .normalBk();
  }
}
.isActive {
  background: #0056dc !important;
}

/**分页**/
/deep/ .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #0056dc;
}
/deep/ .el-pagination.is-background .el-pager li,
/deep/ .el-pagination.is-background .btn-next,
/deep/ .el-pagination.is-background .btn-prev {
  background-color: #003681;
  color: #fff;
}

/deep/ .el-table__fixed-right::before,
/deep/ .el-table__fixed::before {
  background-color: transparent;
}
/deep/ .el-table__fixed-right {
  background: #011f59;
}
/deep/ .el-table__body tr.hover-row > td.el-table__cell {
  background-color: transparent;
}
</style>